@tailwind base;
@tailwind components;
@tailwind utilities;

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f3f7f9;
}

::-webkit-scrollbar-thumb {
  background: #8b99a7;
}

::-webkit-scrollbar-thumb:hover {
  background: #343a40;
}

@layer components {
    body {
        @apply flex flex-col justify-center items-center font-Inter text-base text-neutral-900 bg-neutral-50;
    }

    .text-display {
        @apply font-Inter font-bold text-3xl 2xl:text-8xl md:text-7xl sm:text-5xl;
    }

    .btn-group {
        @apply flex flex-col lg:flex-row lg:gap-3;
    }

    .btn-primary {
        @apply font-Inter font-medium bg-blue-600 hover:bg-blue-500 text-neutral-50 rounded-lg py-3 mb-4 lg:mb-0 lg:px-8 lg:py-5;
    }

    .btn-secondary {
        @apply py-2 border-solid border-2 border-neutral-900 rounded-lg lg:px-8 lg:py-5 hover:opacity-75;
    }

    .text-body {
        @apply text-neutral-800 text-sm lg:text-lg;
    }

    .screen-grid {
        @apply px-3 lg:px-12 xl:px-28;
    }

    .home {
        @apply mt-20 lg:mt-24 xl:mt-28 flex flex-col-reverse items-center lg:flex-row lg:justify-around;
    }

    .reasons {
        @apply w-full flex flex-col lg:flex-row gap-5 lg:gap-16 lg:justify-center sm:items-center;
    }

    .why-us {
        @apply w-full mt-14 lg:mt-16 xl:mt-20;
    }

    .interest {
        @apply w-full mt-20 lg:mt-32 flex justify-center items-end;
    }

    .service {
        @apply mt-16 lg:mt-20 flex flex-col lg:flex-row items-center lg:justify-around;
    }

    .cta {
        @apply w-full flex justify-center items-end mt-24 lg:mt-36;
    }

    .upper-layout-cta {
        @apply absolute flex flex-col lg:flex-row lg:justify-center lg:items-end items-center mb-4 px-3 lg:px-0;
    }

    .upper-layout-interest {
        @apply absolute flex flex-col lg:flex-row justify-end lg:justify-center items-center lg:items-end;
    }

    .heading-1 {
        @apply font-bold text-2xl lg:text-6xl md:text-4xl text-center;
    }

    .heading-1-interest {
        @apply font-bold text-6xl lg:text-9xl text-blue-600;
    }

    .heading-1-service {
        @apply font-bold text-2xl lg:text-5xl md:text-3xl text-center lg:text-start mt-4;
    }

    .heading-1-cta {
        @apply font-bold text-2xl lg:text-4xl md:text-3xl text-start lg:text-start mt-4 sm:text-center;
    }

    .footer {
        @apply w-full bg-neutral-900 text-neutral-50 py-5;
    }

    .copyright {
        @apply flex flex-col lg:flex-row gap-4 justify-center lg:justify-between mt-4 lg:mt-8;
    }

    .copy-year {
        @apply font-Inter font-bold text-xs md:text-sm text-neutral-100;
    }

    .information {
        @apply flex flex-col lg:flex-row gap-4 justify-center lg:justify-between;
    }

    .header-style {
        @apply fixed z-50 top-0 left-0 w-full h-12 lg:h-16 xl:h-20 px-3 lg:px-12 xl:px-28 flex justify-between items-center bg-neutral-50;
    }

    .text-logo-style {
        @apply font-Inter font-bold text-neutral-900 tracking-widest text-sm lg:text-lg;
    }

    .text-icon {
        @apply flex items-center gap-5;
    }

    .btn-size {
        @apply px-6 py-3 lg:px-8 lg:py-5;
    }

    nav {
        @apply absolute lg:relative lg:block hidden right-0 lg:right-0 top-12 lg:top-0 w-1/2 bg-neutral-50 px-3 lg:px-0 py-4 lg:py-0 rounded-b-lg;
    }

    ul {
        @apply text-sm text-neutral-800 font-Inter font-medium flex items-center flex-col lg:flex-row lg:justify-end lg:gap-4;
    }

    .list-direction {
        @apply cursor-pointer w-full lg:w-auto lg:px-8 py-3 hover:text-neutral-900 hover:font-bold text-neutral-900;
    }

    .icon-style-reasons {
        @apply bg-blue-100 px-2 py-2 rounded-full;
    }

    .group-list-footer {
        @apply font-Inter text-sm md:text-base mt-1 flex flex-col items-center lg:items-start;
    }

    .section-information {
        @apply text-center lg:text-start;
    }

    .heading-information-footer {
        @apply font-Inter font-bold text-base md:text-xl;
    }

    .list-text-footer {
        @apply cursor-pointer text-neutral-100 hover:text-neutral-50;
    }
}